<template>
  <div>
     <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pages.has_pre}">
          <a class="page-link" href="#" aria-label="Previous"  @click.prevent="getProducts(pages.current_page -1)">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pages.total_pages" :key="page"
        :class="{'convert':pages.current_page === page}">
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
        </li>
        <li class="page-item" :class="{'disabled': !pages.has_next}">
          <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pages.current_page +1)">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  props:["pages"],
  
  data(){
    return {
      pagination:{}
    }
  },
  methods:{
    getProducts(item){
      this.$emit('getProducts', item)
    }
  }
}
</script>
<style>
  .convert{
    background-color: rgb(77, 202, 108)!important;
    border-color:rgb(158, 224, 178)!important;
  }
</style>
